<template lang="pug">
#accolades 
  .content-container(
    :style="{ opacity: showEl ? 1 : 0, transform: showEl ? 'translateY(0px)' : 'translateY(-20px)' }"
  )
    .heading 
      .heading-row
        p Accolades &amp; #[br] Achievements
        .heading-row-line.small-up
      .heading-column
        .heading-column-line.small-up
        .inner-content 
          .flex-container
            .column-container
              .image-container
                img(src="../assets/accolades/mdec.jpg")
            .text-container
              p Won 1st place in a regional hackathon called The Grand Challenge by MDEC.
          .flex-container-2
            .text-container
              p Invited to "The Next Big Tech Asia" - an international conference and exhibition - to pitch our solution to angel investors.
            .column-container 
              .image-container
                img(src="../assets/accolades/big-tech-asia-cropped.jpg")
          .flex-container.small-up
            .column-container 
              .image-container-code
                img(src="../assets/accolades/unicode.jpeg")
            .column-container
              .image-container-code
                img(src="../assets/accolades/leetcode.svg")
          .flex-container.small-up
            .text-container
              p Achieved Top 10 in a university-level competitive programming competition.
            .text-container 
              p I also participate in Leetcode's weekly &amp; biweekly contests.
          .flex-container.mobile
            .column-container 
              .image-container-code
                img(src="../assets/accolades/unicode.jpeg")
            .text-container
              p Achieved Top 10 in a university-level competitive programming competition.
          .flex-container.mobile
            .column-container 
              .image-container-code
                img(src="../assets/accolades/leetcode.svg") 
            .text-container
              p I also participate in Leetcode's weekly &amp; biweekly contests.
</template>

<script>
export default {
  data() {
    return {
      showEl: false,
    };
  },
  methods: {
    initIntersectionObserver() {
      // eslint-disable-next-line
      const observer = new IntersectionObserver(
        (entries) => {
          if (entries[0].intersectionRatio > 0) {
            if (!this.showEl) {
              this.showEl = true;
            }
            // console.log("Cross Accolades");
          }
        },
        {
          // rootMargin: "0px 0px -200px 0px",
          threshold: 0.5,
        }
      );

      observer.observe(document.querySelector("#accolades"));

      this.$store.state.intersectionObserver.observe(
        document.querySelector("#accolades")
      );
    },
  },
  mounted() {
    setTimeout(() => {
      this.initIntersectionObserver();
    }, 2000);
  },
};
</script>

